import * as React from 'react';
import { Container, Grid, Button } from '@material-ui/core';
import 'fontsource-roboto';
import WordComponent from './WordComponent';

interface AppProps {
}

interface AppState {
  slides?: {
    word: string;
    trans: string;
    sentence: string;
    sentenceTrans: string;
  }[];
  wordIndex?: number;
  visibleTrans?: string;
  visibleSentenceTrans?: string;
}
class App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      slides: [
        {'word':'I', 'trans':'私は', 'sentence': "I have a pen.", 'sentenceTrans': '私はペンを持っています。'},
        {'word':'my', 'trans':'私の', 'sentence': "You are my friend.", 'sentenceTrans': '貴方は私の友達です。'}, 
        {'word':'me', 'trans':'私を', 'sentence': "Ken doesn't know me.", 'sentenceTrans': 'ケンは私を知りません。'},
      ],
      wordIndex: 1,
      visibleTrans: 'translate-hidden',
      visibleSentenceTrans: 'translate-hidden'
    }
  }

  render () {
    const slidePrev = () => {
      if (this.state.wordIndex === undefined) return;
      if (this.state.wordIndex > 0) {
        this.setState({
          wordIndex: this.state.wordIndex - 1,
          visibleTrans: 'translate-hidden',
          visibleSentenceTrans: 'translate-hidden'
        })
      }
    };
  
    const slideNext = () => {
      if (this.state.wordIndex === undefined || this.state.slides === undefined) return;

      if (this.state.wordIndex < this.state.slides.length-1) {
        this.setState({
          wordIndex: this.state.wordIndex + 1,
          visibleTrans: 'translate-hidden',
          visibleSentenceTrans: 'translate-hidden'
        })
      }
    };
    
    const slides = this.state.slides ?? [] ;
    const wordIndex = this.state.wordIndex ?? 0;
    const visibleTrans = this.state.visibleTrans ?? 'translate-hidden';
    const visibleSentenceTrans = this.state.visibleSentenceTrans ?? 'translate-hidden';
    return (
      <Container maxWidth="sm">
        <WordComponent
          word={slides[wordIndex].word}
          trans={slides[wordIndex].trans}
          visibleTrans = {visibleTrans}
          onClick = {() => this.setState({visibleTrans:'translate-visible'})}
        />
        <hr/>
        <WordComponent
          word={slides[wordIndex].sentence}
          trans={slides[wordIndex].sentenceTrans}
          visibleTrans = {visibleSentenceTrans}
          onClick = {() => this.setState({visibleSentenceTrans:'translate-visible'})}
        />
        <hr/>
        <Grid
          container
          direction="row"
          justify="space-around"
          alignItems="flex-end"
        >
          <Button
            variant="contained"
            color="primary"
            onClick={() => {slidePrev()}}
          >
            Prev
          </Button> 
          <Button 
            variant="contained"
            color="primary"
            onClick={() => {slideNext()}}
          >
            Next
          </Button>
        </Grid>
      </Container>
    );
  }
}

export default App;