import * as React from 'react';
import { Container, Button } from '@material-ui/core';
import 'fontsource-roboto';

interface WordComponentProps {
    word: string;
    trans: string;
    visibleTrans: string;
    onClick: () => void;
}

class WordComponent extends React.Component<WordComponentProps, {}>{
  render () {
    const style: React.CSSProperties = {
      display: 'inline-block',
      width: '100%',
      height: '20%',
    }

    const voices = window.speechSynthesis.getVoices().filter(voice => voice.lang === 'en-US');
    const voice = voices[0];

    return (
      <div className="word-wrap">
        <Container maxWidth="sm">
          <div onClick={() => this.props.onClick()}>
            <p className="word">{this.props.word}</p>
          </div>
          <div
            className={this.props.visibleTrans}
          >
            {this.props.trans}
          </div>
          <Button
            style={style}
            onClick={ () => {
              const utter = new SpeechSynthesisUtterance();
              utter.text = this.props.word;
              utter.lang = "en-US";
              utter.voice = voice;
              window.speechSynthesis.speak(utter);
            }}
          >
            play
          </Button>
        </Container>
      </div>
    );
  }
}

export default WordComponent;